import React, { useState, useRef } from "react";
import "./styles.css";
import ReactMarkdown from 'react-markdown';
import Footer from "./Footer";

const LandingPage = () => {
  const [resumeText, setResumeText] = useState("");
  const [evaluation, setEvaluation] = useState(null);
  const [photo, setPhoto] = useState(null);
  const textareaRef = useRef(null);
  const photoPreviewRef = useRef(null);

  const handleTextChange = (event) => {
    setResumeText(event.target.value);
    autoResizeTextarea();
  };

  const handlePhotoChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPhoto(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const autoResizeTextarea = () => {
    if (textareaRef.current) {
      textareaRef.current.style.height = "auto";
      textareaRef.current.style.height = textareaRef.current.scrollHeight + "px";
    }
  };

  const evaluateResume = async () => {
    try {
      const textWithPhoto = photo ? "  .\n" + resumeText : "   .\n" + resumeText;
      
      const requestBody = {
        modelUri: "gpt://b1go16afjpn93m7ve6pe/yandexgpt/latest",
        completionOptions: {
          stream: false,
          temperature: 0.75,
          maxTokens: "1200"
        },
        messages: [
          {
            role: "user",
            text: textWithPhoto
          }
        ]
      };

      const response = await fetch("https://functions.yandexcloud.net/d4eu2993fe4u9pmt13gk", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(requestBody)
      });

      if (!response.ok) {
        console.log(response);
        throw new Error("   ");
      }

      const data = await response.json();
      setEvaluation(data.result.alternatives[0].message.text);
    } catch (error) {
      console.error(error);
      setEvaluation(null);
    }
  };

  return (
    <div className="container">
      <div className="header">
        <h1 className="title">CV Eval</h1>
        <h3 className="undertitle">       </h3>
      </div>
      <div className="content">
        <div className="form">
          <div className="inputdiv">
            <textarea
              ref={textareaRef}
              className="textarea"
              placeholder="  "
              value={resumeText}
              onChange={handleTextChange}
            />
            <div className="photoinputdiv">
              <label htmlFor="file-upload" className="file-upload-label">
                <input id="file-upload" type="file" accept="image/*" onChange={handlePhotoChange} />
                 
              </label>
              {photo && (
                <img src={photo} alt="Uploaded" className="photo-preview" ref={photoPreviewRef} />
              )}
            </div>
          </div>
          <button className="submit-button" onClick={evaluateResume}>  </button>
        </div>
        {evaluation && (
          <div>
            <hr className="solid"/>
            <h2 className="subtitle"> :</h2>
            <ReactMarkdown className="markdown">{evaluation}</ReactMarkdown>
          </div>
        )}
        <hr className="solid"/>
        <div className="project-info">
          <h2 className="subtitle"> </h2>
          <p>
                CV Eval,            !
          </p>
          <p>
            CV Eval -    ,   ,               .
              -              .
          </p>
          <p>
               ,   24/7    .   - .      ,        ,
                ,        .
          </p>
          <p>
              ?    ,      ,    ,         
            ,    .
          </p>
          <p>
              ,     CV Eval!                !
          </p>
          <p>
            *   ,      
          </p>
        </div>
        </div>
      <Footer/>
    </div>
  );
};

export default LandingPage;
